import instance from "@/utils/requestUtils";


let roleApi = {
    //分页条件查询
    searchPage(searchForm) {
        return instance.get('role', {params: searchForm})
    },

    findById(id) {
        return instance.get(`role/${id}`)
    },

    add(role) {
        return instance.post(`role`, role);
    },
    update(role) {
        return instance.put(`role`, role);
    },
    deleteById(id) {
        return instance.delete(`role/${id}`)
    },
    batchDelete(ids) {
        return instance.delete(`role/batch/${ids}`)
    },
    getAllMenuList(){
        return instance.get('role/allMenuList')
    },
    getMenuListByRoleId(id) {
        return instance.get(`role/menuList/${id}`);
    },
    saveMenuToRole(treeForm) {
        return instance.put('role/menu',treeForm)
    }
}

export default roleApi;
