import menuApi from "../../api/menuApi";
import dateTime from "@/utils/dateutils";
import selectElicon from "./selectElicon";
// import fa from "element-ui/src/locale/lang/fa";

export default {
    components: {selectElicon},
    name: "index",
    data() {
        return {
            pickerOptions: dateTime.pickerOptions,
            value2: dateTime.value2,
            searchForm: {
                currentPage: 1,
                pageSize: 5,
            },
            tableData: [],
            total: 0,
            editDialog: false,
            formData: {
                menuIcon: "",
                menuType:0,
            },
            ids: [],
            menuTypeList:[
                {id:0,label:"目录"},{id:1,label:"菜单"},{id:2,label:"按钮"}
            ],
            menuListOptions:[{
                id:0,
                menuTitle: "主菜单",
                children:[]
            }],

            permBtn: {
                id:8,
                add:false,
                edit:false,
                del:false
            },
            //表单校验的独享
            formRules: {
                menuTitle: [
                    {required: true, message: '请输入菜单名称', trigger: 'blur'},
                ],
                parentId: [
                    {required: true, message: '请选择父菜单', trigger: 'change'},
                    {type: 'number', message: '请选择正确的父菜单', trigger: 'change'},
                ],
                menuType: [
                    {required: true, message: '请选择菜单类型', trigger: 'blur'},
                ],
                permSign: [
                    {required: true, message: '请输入权限标识', trigger: 'blur'},
                ]
            }
        }
    },
    created() {
        this.searchPage();
        this.getPerm()
    },

    methods: {

        async getPerm(){
            let menus = JSON.parse(localStorage.getItem("menus"))
            let perm = this.foundMenuChild(this.permBtn.id,menus)
            console.log(perm)
            if (perm&&perm.children){
                for (let btn of perm.children){
                    if (btn.permSign === 'sys:menu:add'){
                        this.permBtn.add = true
                    }
                    if (btn.permSign === 'sys:menu:edit'){
                        this.permBtn.edit = true
                    }
                    if (btn.permSign === 'sys:menu:del'){
                        this.permBtn.del = true
                    }
                }
            }
        },
        //////////////////////业务逻辑的方法//////////////////
        /*分页条件查询*/
        async searchPage() {
            let result = await menuApi.searchPage(this.searchForm);
            this.tableData = result.list;
            this.total = result.total;
            this.menuListOptions[0].children = await menuApi.getSelectMenuList()
        },


        /*添加或者修改品牌*/
        addOrEdit() {
            this.$refs.form.validate(async (valid) => {
                if (valid) {
                    if (this.formData.id) {
                        await menuApi.updateMenu(this.formData);
                    } else {
                        await menuApi.addMenu(this.formData);
                    }
                    this.searchPage();
                    this.editDialog = false;
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });


        },

        /*删除品牌*/
        async batchDelete() {
            await menuApi.batchDelete(this.ids);
            this.searchPage();
        },

        /**通过id查询*/
        async findById(id) {
            this.formData = await menuApi.findById(id);
        },


        //////////////////////处理页面效果////////////////////////////
        chooseTime(value) {
            this.searchForm.startTime = value[0];
                this.searchForm.endTime = value[1]
        },




        /*点击分页效果*/
        pageChangeHandler(page) {
            this.searchForm.currentPage = page;
            this.searchPage();
        },
        /*重置查询表单*/
        resetSearchForm() {
            this.searchForm = {
                currentPage: 1,
                pageSize: 5,
            };

            this.value2 = [];
        },
        /*选择图标*/
        changeIcon(e){
            this.formData.menuIcon = e
        },
        /*点击列表中的点击删除*/
        itemBtnClick(row) {
            this.$refs.table.clearSelection();
            this.$refs.table.toggleRowSelection(row)
            this.ids = [];
            this.ids.push(row.id);
        },
        /*勾选框发生改变*/
        selectChange(selection) {
            console.log(selection)
            this.ids = [];
            var map = selection.map(item => item.id);
            map.forEach(item => {
                this.ids.push(item)
            })
        },
        /*点击批量删除*/
        doBatchDelete() {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                //做删除的功能
                this.batchDelete();
            }).catch(() => {
                //取消的功能
                this.$refs.table.clearSelection();
                this.ids = []
            });
        },

        /**
         * 取消删除
         */
        batchDeleteCancel() {
            this.$refs.table.clearSelection();
        }
    }
}
