import instance from "@/utils/requestUtils";


let brandApi = {
    //分页条件查询
    searchPage(searchForm) {
        return instance.get('brand', {params: searchForm})
    },

    findById(id) {
        return instance.get(`brand/${id}`)
    },

    addBrand(brand) {
        return instance.post(`brand`, brand);
    },
    updateBrand(brand) {
        return instance.put(`brand`, brand);
    },
    deleteById(id) {
        return instance.delete(`brand/${id}`)
    },
    batchDelete(ids) {
        return instance.delete(`brand/batch/${ids}`)
    }

}

export default brandApi;
