
import roleApi from "../../api/roleApi";
import dateTime from "@/utils/dateutils";


export default {
    name: "index",
    data() {
        return {
            pickerOptions: dateTime.pickerOptions,
            value2: dateTime.value2,
            searchForm: {
                currentPage: 1,
                pageSize: 5,
            },
            tableData: [],
            treeData:[],
            treeForm: {
                roleId:undefined,
                menuIds:[]},
            total: 0,
            editDialog: false,
            formData: {},
            ids: [],

            permBtn: {
                id:6,
                add:false,
                edit:false,
                del:false,
                perm:false
            },
            //表单校验的独享
            formRules: {
                roleName: [
                    {required: true, message: '角色名称不能为空', trigger: 'blur'},
                ],
                roleDesc: [
                    {required: true, message: '角色描述不能为空', trigger: 'blur'},
                ],
            }
        }
    },
    created() {
        this.searchPage();
        this.getPerm()
    },

    methods: {

        async getPerm(){
            let menus = JSON.parse(localStorage.getItem("menus"))
            let perm = this.foundMenuChild(this.permBtn.id,menus)
            console.log(perm)
            if (perm&&perm.children){
                for (let btn of perm.children){
                    if (btn.permSign === 'sys:role:add'){
                        this.permBtn.add = true
                    }
                    if (btn.permSign === 'sys:role:edit'){
                        this.permBtn.edit = true
                    }
                    if (btn.permSign === 'sys:role:del'){
                        this.permBtn.del = true
                    }
                    if (btn.permSign === 'sys:role:grant'){
                        this.permBtn.perm = true
                    }
                }
            }
        },

        getMenuIds(){
            this.treeForm.menuIds = []
            Array.prototype.push.apply(this.treeForm.menuIds,this.$refs.menuTree.getCheckedKeys(false))
            Array.prototype.push.apply(this.treeForm.menuIds,this.$refs.menuTree.getHalfCheckedKeys())

            // this.treeForm.menuIds.push.apply(this.$refs.menuTree.getCheckedKeys(false))
            // this.treeForm.menuIds.push.apply(this.$refs.menuTree.getHalfCheckedKeys())
        },

        roleClick(row){
            this.treeForm.roleId =row.id
            this.findMenuListById(this.treeForm.roleId)
        },

        //////////////////////业务逻辑的方法//////////////////
        /*分页条件查询*/
        async searchPage() {
            let result = await roleApi.searchPage(this.searchForm);
            this.tableData = result.data;
            this.total = result.total;
            this.treeData = await roleApi.getAllMenuList()
        },




        /*添加或者修改品牌*/
        addOrEdit() {
            this.$refs.form.validate(async (valid) => {
                if (valid) {
                    if (this.formData.id) {
                        await roleApi.update(this.formData);
                    } else {
                        await roleApi.add(this.formData);
                    }
                    await this.searchPage();
                    this.editDialog = false;
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });


        },

        async saveMenuToRole(){
            await roleApi.saveMenuToRole(this.treeForm);
            await this.searchPage();
            this.treeForm.roleId = undefined
            this.$refs.table.setCurrentRow()
        },
        
        
        /*删除品牌*/
        async batchDelete() {
            await roleApi.batchDelete(this.ids);
            this.searchPage();
        },

        /**通过id查询*/
        async findById(id) {
            this.formData = await roleApi.findById(id);
        },

        async findMenuListById(id){
            if (this.permBtn.perm){
                this.treeForm.menuIds = await roleApi.getMenuListByRoleId(id)
                this.$refs.menuTree.setCheckedKeys([])
                for (let menuId of this.treeForm.menuIds) {
                    this.$refs.menuTree.setChecked(menuId,true,false)
                }
            }
        },
        

        //////////////////////处理页面效果////////////////////////////
        chooseTime(value) {
            this.searchForm.startTime = value[0],
                this.searchForm.endTime = value[1]
        },


        /*点击分页效果*/
        pageChangeHandler(page) {
            this.searchForm.currentPage = page;
            this.searchPage();
        },
        /*重置查询表单*/
        resetSearchForm() {
            this.searchForm = {
                currentPage: 1,
                pageSize: 5,
            };

            this.value2 = [];
        },
        /*点击列表中的点击删除*/
        itemBtnClick(row) {
            this.$refs.table.clearSelection();
            this.$refs.table.toggleRowSelection(row)
            this.ids = [];
            this.ids.push(row.id);
        },
        /*勾选框发生改变*/
        selectChange(selection) {
            console.log(selection)
            this.ids = [];
            var map = selection.map(item => item.id);
            map.forEach(item => {
                this.ids.push(item)
            })
        },
        /*点击批量删除*/
        doBatchDelete() {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                //做删除的功能
                this.batchDelete();
            }).catch(() => {
                //取消的功能
                this.$refs.table.clearSelection();
                this.ids = []
            });
        },

        /**
         * 取消删除
         */
        batchDeleteCancel() {
            this.$refs.table.clearSelection();
        }
    }
}
